"use client";

import React, { useState, useEffect } from "react";
import Link from "next/link";

const initialFoods = [
    { id: 1, name: "Yumurta (Haşlanmış)", cal: 155, protein: 13, carb: 1.1, fat: 11, unit: "100g" },
    { id: 2, name: "Yulaf Ezmesi", cal: 389, protein: 16.9, carb: 66, fat: 6.9, unit: "100g" },
    { id: 3, name: "Tavuk Göğsü (Izgara)", cal: 165, protein: 31, carb: 0, fat: 3.6, unit: "100g" },
    { id: 4, name: "Avokado", cal: 160, protein: 2, carb: 9, fat: 15, unit: "100g" },
    { id: 5, name: "Ceviz", cal: 654, protein: 15, carb: 14, fat: 65, unit: "100g" },
    { id: 6, name: "Beyaz Peynir (Tam Yağlı)", cal: 289, protein: 14, carb: 2.5, fat: 24, unit: "100g" },
];

export default function FoodLibrary() {
    const [foods, setFoods] = useState<any[]>([]);
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [editId, setEditId] = useState<number | null>(null);
    const [formData, setFormData] = useState({ name: "", unit: "100g", cal: "", protein: "", carb: "", fat: "" });
    const [searchTerm, setSearchTerm] = useState("");

    useEffect(() => {
        const saved = localStorage.getItem("foods");
        if (saved) {
            setFoods(JSON.parse(saved));
        } else {
            setFoods(initialFoods);
            localStorage.setItem("foods", JSON.stringify(initialFoods));
        }
    }, []);

    const handleSave = () => {
        if (!formData.name) return alert("Lütfen besin adı girin.");

        let newFoods;
        if (editId) {
            newFoods = foods.map(f => f.id === editId ? {
                ...f,
                name: formData.name,
                unit: formData.unit,
                cal: Number(formData.cal),
                protein: Number(formData.protein),
                carb: Number(formData.carb),
                fat: Number(formData.fat)
            } : f);
        } else {
            const newId = foods.length > 0 ? Math.max(...foods.map(f => f.id)) + 1 : 1;
            newFoods = [{
                id: newId,
                name: formData.name,
                unit: formData.unit,
                cal: Number(formData.cal),
                protein: Number(formData.protein),
                carb: Number(formData.carb),
                fat: Number(formData.fat)
            }, ...foods];
        }

        setFoods(newFoods);
        localStorage.setItem("foods", JSON.stringify(newFoods));
        closeModal();
    };

    const handleDelete = (id: number) => {
        if (window.confirm("Bu besini silmek istediğinize emin misiniz?")) {
            const newFoods = foods.filter(f => f.id !== id);
            setFoods(newFoods);
            localStorage.setItem("foods", JSON.stringify(newFoods));
        }
    };

    const openEditModal = (food: any) => {
        setFormData({
            name: food.name,
            unit: food.unit,
            cal: food.cal.toString(),
            protein: food.protein.toString(),
            carb: food.carb.toString(),
            fat: food.fat.toString()
        });
        setEditId(food.id);
        setIsModalOpen(true);
    };

    const closeModal = () => {
        setIsModalOpen(false);
        setEditId(null);
        setFormData({ name: "", unit: "100g", cal: "", protein: "", carb: "", fat: "" });
    };

    const filteredFoods = foods.filter(f => f.name.toLowerCase().includes(searchTerm.toLowerCase()));

    return (
        <div style={{ minHeight: "100vh", background: "#f4f7f4", display: "flex", fontFamily: "var(--font-body)" }}>

            {/* Admin Sidebar */}
            <div style={{ width: "280px", background: "#2c3e50", color: "white", padding: "40px 30px", display: "flex", flexDirection: "column", gap: "40px" }}>
                <div>
                    <h2 style={{ fontSize: "22px", fontWeight: 800, color: "#79a33d" }}>Sümeyye Hanım</h2>
                    <p style={{ fontSize: "12px", color: "#bdc3c7", marginTop: "5px" }}>Diyetisyen Admin Paneli</p>
                </div>

                <nav style={{ display: "flex", flexDirection: "column", gap: "12px" }}>
                    <Link href="/admin" style={navLink}>Genel Bakış</Link>
                    <Link href="/admin/clients" style={navLink}>Danışan Yönetimi</Link>
                    <Link href="/admin/food" style={activeNavLink}>Besin Kütüphanesi</Link>
                    <Link href="/admin/appointments" style={navLink}>Randevu Takvimi</Link>
                    <Link href="/admin/reports" style={navLink}>Raporlar</Link>
                </nav>

                <div style={{ marginTop: "auto" }}>
                    <Link href="/" style={{ color: "#ecf0f1", textDecoration: "none", fontSize: "14px", opacity: 0.8 }}>Oturumu Kapat</Link>
                </div>
            </div>

            {/* Main Content */}
            <div style={{ flex: 1, padding: "50px", overflowY: "auto", position: "relative" }}>

                <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center", marginBottom: "40px" }}>
                    <div>
                        <h1 style={{ fontSize: "32px", fontWeight: 700, color: "#2c3e50" }}>Besin Kütüphanesi</h1>
                        <p style={{ color: "#7f8c8d" }}>Diyet listelerinde kullanacağınız ürünlerin değerlerini buradan yönetin.</p>
                    </div>
                    <button onClick={() => setIsModalOpen(true)} style={{
                        background: "#79a33d",
                        color: "white",
                        border: "none",
                        padding: "12px 25px",
                        borderRadius: "10px",
                        fontWeight: 700,
                        cursor: "pointer",
                        boxShadow: "0 10px 20px rgba(121, 163, 61, 0.2)"
                    }}>+ Yeni Besin Ekle</button>
                </div>

                {/* Search Bar */}
                <div style={{ marginBottom: "30px" }}>
                    <input
                        type="text"
                        placeholder="Besin ismi ile ara..."
                        value={searchTerm}
                        onChange={(e) => setSearchTerm(e.target.value)}
                        style={{
                            width: "100%",
                            padding: "15px 20px",
                            borderRadius: "12px",
                            border: "1px solid #ddd",
                            outline: "none",
                            fontSize: "15px",
                            color: "#333",
                            fontWeight: 600
                        }}
                    />
                </div>

                {/* Food Table */}
                <div style={{ background: "white", borderRadius: "20px", overflow: "hidden", boxShadow: "0 10px 20px rgba(0,0,0,0.02)" }}>
                    <table style={{ width: "100%", borderCollapse: "collapse" }}>
                        <thead style={{ background: "#fcfdfe" }}>
                            <tr style={{ textAlign: "left" }}>
                                <th style={thStyle}>Besin Adı</th>
                                <th style={thStyle}>Birim</th>
                                <th style={thStyle}>Kalori (kcal)</th>
                                <th style={thStyle}>Protein (g)</th>
                                <th style={thStyle}>Karbonhidrat (g)</th>
                                <th style={thStyle}>Yağ (g)</th>
                                <th style={thStyle}>İşlemler</th>
                            </tr>
                        </thead>
                        <tbody>
                            {filteredFoods.map((food) => (
                                <tr key={food.id} style={{ borderBottom: "1px solid #f1f1f1" }}>
                                    <td style={tdStyle}><strong>{food.name}</strong></td>
                                    <td style={tdStyle}>{food.unit}</td>
                                    <td style={tdStyle}>{food.cal}</td>
                                    <td style={tdStyle}>{food.protein}</td>
                                    <td style={tdStyle}>{food.carb}</td>
                                    <td style={tdStyle}>{food.fat}</td>
                                    <td style={tdStyle}>
                                        <div style={{ display: "flex", gap: "10px" }}>
                                            <button onClick={() => openEditModal(food)} style={{ ...actionBtn, color: "#3498db" }}>Düzenle</button>
                                            <button onClick={() => handleDelete(food.id)} style={{ ...actionBtn, color: "#e74c3c" }}>Sil</button>
                                        </div>
                                    </td>
                                </tr>
                            ))}
                            {filteredFoods.length === 0 && (
                                <tr>
                                    <td colSpan={7} style={{ textAlign: "center", padding: "40px", color: "#95a5a6" }}>
                                        Arama kriterlerine uygun besin bulunamadı.
                                    </td>
                                </tr>
                            )}
                        </tbody>
                    </table>
                </div>

                {/* Add/Edit Modal */}
                {isModalOpen && (
                    <div style={{ position: "fixed", top: 0, left: 0, width: "100%", height: "100%", background: "rgba(0,0,0,0.5)", display: "flex", alignItems: "center", justifyContent: "center", zIndex: 1000 }}>
                        <div style={{ background: "white", padding: "40px", borderRadius: "20px", width: "500px", maxWidth: "90%", boxShadow: "0 20px 40px rgba(0,0,0,0.2)" }}>
                            <h2 style={{ fontSize: "24px", fontWeight: 700, color: "#2c3e50", marginBottom: "25px" }}>
                                {editId ? "Besini Düzenle" : "Yeni Besin Ekle"}
                            </h2>

                            <div style={{ display: "flex", flexDirection: "column", gap: "15px", marginBottom: "30px" }}>
                                <input placeholder="Besin Adı" value={formData.name} onChange={e => setFormData({ ...formData, name: e.target.value })} style={inputStyle} />
                                <input placeholder="Birim (örn: 100g, 1 Porsiyon)" value={formData.unit} onChange={e => setFormData({ ...formData, unit: e.target.value })} style={inputStyle} />
                                <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr", gap: "15px" }}>
                                    <input type="number" placeholder="Kalori (kcal)" value={formData.cal} onChange={e => setFormData({ ...formData, cal: e.target.value })} style={inputStyle} />
                                    <input type="number" placeholder="Protein (g)" value={formData.protein} onChange={e => setFormData({ ...formData, protein: e.target.value })} style={inputStyle} />
                                    <input type="number" placeholder="Karbonhidrat (g)" value={formData.carb} onChange={e => setFormData({ ...formData, carb: e.target.value })} style={inputStyle} />
                                    <input type="number" placeholder="Yağ (g)" value={formData.fat} onChange={e => setFormData({ ...formData, fat: e.target.value })} style={inputStyle} />
                                </div>
                            </div>

                            <div style={{ display: "flex", gap: "15px", justifyContent: "flex-end" }}>
                                <button onClick={closeModal} style={{ padding: "12px 20px", background: "#f0f0f0", color: "#666", border: "none", borderRadius: "10px", fontWeight: 600, cursor: "pointer" }}>İptal</button>
                                <button onClick={handleSave} style={{ padding: "12px 25px", background: "#79a33d", color: "white", border: "none", borderRadius: "10px", fontWeight: 600, cursor: "pointer" }}>Kaydet</button>
                            </div>
                        </div>
                    </div>
                )}
            </div>
        </div>
    );
}

const thStyle: React.CSSProperties = {
    padding: "20px",
    color: "#95a5a6",
    fontSize: "13px",
    textTransform: "uppercase",
    letterSpacing: "1px",
    fontWeight: 700
};

const tdStyle: React.CSSProperties = {
    padding: "20px",
    color: "#2c3e50",
    fontSize: "15px"
};

const actionBtn: React.CSSProperties = {
    background: "none",
    border: "none",
    cursor: "pointer",
    fontSize: "15px",
    fontWeight: 700,
    padding: "5px 10px",
    borderRadius: "5px",
    transition: "background 0.3s"
};

const navLink: React.CSSProperties = {
    padding: "15px 20px",
    borderRadius: "12px",
    color: "#bdc3c7",
    textDecoration: "none",
    fontSize: "15px",
    transition: "all 0.3s ease",
};

const activeNavLink: React.CSSProperties = {
    ...navLink,
    background: "#34495e",
    color: "white",
    fontWeight: 700
};

const inputStyle: React.CSSProperties = {
    width: "100%",
    padding: "12px 15px",
    borderRadius: "8px",
    border: "1px solid #ddd",
    fontSize: "14px",
    color: "#333",
    outline: "none",
    fontWeight: 500
};
